@import "../components/variables";

@media screen and (max-width: 1550px), screen and (max-device-width: 1550px) {
  body {
    .product__card {
      .container {
        grid-template-columns: 25% 40% 35%;
        .content__productCard__packagingOptions{
          margin: 75px 0 0;
          .packaging,
          .plate{
            button{
              width: 105px;
            }
          }
          .consultation{
            a{
              width: 245px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1500px), screen and (max-device-width: 1500px){
  body{
    .product__card{
      .container{
        .content__recomend{
          .slider{
            height: 300px;
            ul{
              li{
                height: 300px;
                .hover{
                  .addBusket,
                  .addFavorite{
                    margin: 0 10px 60px 0;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1310px), screen and (max-device-width: 1310px) {
  body {
    .product__card {
      .container {
        .content__productCard__img{
          .slideshow{
            li{
              height: 250px;
            }
          }
        }
        .content__productCard__packagingOptions{
          margin: 63px 0 0;
          .productInfo{
            div{
              p,
              span{
                height: 20px;
              }
            }
          }
        }
        .content__recomend{
          .slider {
            height: 325px;
            ul {
              li{
                width: 225px;
                height: 325px;
                a{
                  img{
                    margin-top: 15px;
                    width: 200px;
                    height: 200px;
                  }
                }
                .hover{
                  width: 223px;
                  .addBusket,
                  .addFavorite{
                    margin: 0 10px 45px 0;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

