@media screen and (max-width: 920px), screen and (max-device-width: 920px){
  body{
    .product__card{
      .container{
        grid-template-columns: 55% 45%;
        grid-template-areas: "breadcrumb breadcrumb"
                             "productImg descr"
                             "analogue analogue"
                             "recomend recomend"
                             "aside aside"
                             "order order";
        .content__aside{
          width: 100%;
          div{
            ul{
              height: 200px;
            }
          }
        }
        .content__productCard__img{
          .slideshow{
            li{
              height: 250px;
            }
          }
        }
        .content__productCard__packagingOptions{
          margin: 54px 0 0 15px;
          .packaging,
          .plate{
            justify-content: space-around;
            .value{
              width: 100%;
              order: 1;
            }
            .price{
              width: 50%;
              order: 2;
            }
            .packagingQuantitu,
            .plateQuantitu{
              order: 3;
            }
            button{
              margin: 20px 0 0;
              order: 4;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 780px), screen and (max-device-width: 780px){
  body{
    .product__card{
      .container{
        grid-template-areas: "breadcrumb breadcrumb"
                             "productImg productImg"
                             "descr descr"
                             "analogue analogue"
                             "recomend recomend"
                             "aside aside"
                             "order order";
        .content__productCard__img{
          .slideshow{
            li{
              height: 400px;
            }
          }
        }
        .content__productCard__packagingOptions{
          margin: 25px 0 0 0;
          .packaging,
          .plate{
            button{
              margin: 0;
            }
          }
          .others{
            height: 50px;
            p{
              line-height: 50px;
            }
          }
        }
        .content__recomend{
          .slider{
            .center{
              .prevRecomend{
                left: 0;
              }
              .nextRecomend{
                right: 0;
              }
            }
          }
        }
      }
    }
    .content__productCard__imgPopup{
      grid-template-areas: "slider slider"
                           "options options" ;
      .slideshowPopup{
        .slideshow{
          li{
            height: 250px;
            img{
              width: 80%;
            }
          }
        }
      }
      .packagingOptionsPopup{
        .packaging,
        .plate{
          padding: 0 25px;
        }
      }
    }
  }
}

@media screen and (max-width: 640px), screen and (max-device-width: 640px){
  body{
    .product__card{
      .container{
        .content__productCard__analogue{
          .productCard__descr{
            li{
              margin: 0;
              width: 25%;
              a{
                font-size: .875em;
              }
            }
          }
          .therapeuticEffect,
          .farmCare,
          .manual,
          .videoSurveillance{
            border-radius: 0 0 5px 5px;
          }
        }
      }
    }
  }
}

